import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { getOrders, getOrdersCalculations } from '../../../actions/orderAction'

export default function OrderStats() {
    const [totalOrders, setTotalOrders] = useState(0)
    const [pendingOrders, setPendingOrders] = useState(0)
    const [inProgress, setInProgress] = useState(0)
    const [acceptedOrders, setAcceptedOrders] = useState(0)
    const [completed, setCompleted] = useState(0)
    const [rejectedOrders, setRejectedOrders] = useState(0)
    const dispatch = useDispatch()
    const orders = useSelector(state => state)
    useEffect(() => {
        dispatch(getOrdersCalculations('Canada'))
        console.log(orders)
    }, [])

    return (
        <div className="ibox">
            <div className="ibox-title" style={{ minHeight: 84 }}>
                <h5>Order Statistics</h5>
                <div className='ibox-tools'>
                    <div className='custom-ibox-tools'>
                        <span className='text-uppercase'>Total Orders</span>
                    </div>
                    <div className='custom-ibox-tools'>
                        <span>Canada: </span>
                        <span>1</span>
                    </div>
                    <div className='custom-ibox-tools'>
                        <span>United States:</span>
                        <span>1</span>
                    </div>
                </div>
            </div>

            <div className="card card-dashboard-table">
                <table className="table table-bordered">
                    <thead>
                        <tr>
                            <th>Country</th>
                            <th className='bg-primary'>Accepted</th>
                            <th className='bg-warning' >Pending</th>
                            <th className='bg-info' >Running</th>
                            <th className='bg-success' >Completed</th>
                            <th className='bg-danger' >Rejected</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td><a href="#">United States</a></td>
                            <td><strong>{acceptedOrders}</strong></td>
                            <td><strong>{pendingOrders}</strong></td>
                            <td><strong>{inProgress}</strong></td>
                            <td><strong>{completed}</strong></td>
                            <td><strong>{rejectedOrders}</strong></td>
                        </tr>
                        <tr>
                            <td><a href="#">Canada</a></td>
                            <td><strong>{acceptedOrders}</strong></td>
                            <td><strong>{pendingOrders}</strong></td>
                            <td><strong>{inProgress}</strong></td>
                            <td><strong>{completed}</strong></td>
                            <td><strong>{rejectedOrders}</strong></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    )
}
