import axios from "axios";
import { GET_ORDERS, GET_ORDERS_CALCULATIONS } from "../utilities/constants";

export const getOrders = () => async (dispatch) => {
  const res = await axios.get("/api/orders");
  const orders = await res.data;
  if (orders !== null) {
    dispatch({
      type: GET_ORDERS,
      payload: orders,
    });
  }
};

export const getOrdersCalculations = (country) => async (dispatch) => {
  try {
    const res = await axios.get(`/api/calculations/orders/count-by-country?country=${country}`);
    const { count, pendingOrders, acceptedOrders, startedOrders, completedOrders, rejectedOrders } = res.data;
    dispatch({
      type: GET_ORDERS_CALCULATIONS,
      payload: { count, pendingOrders, acceptedOrders, startedOrders, completedOrders, rejectedOrders },
    });
    console.log("orders", count, pendingOrders, acceptedOrders, startedOrders, completedOrders, rejectedOrders);
  } catch (error) {
    console.error(error);
  }
};
