import { GET_ORDERS, GET_ORDERS_CALCULATIONS } from "../utilities/constants";
const initialState = {
  orders: [],

};
export default function orderReducer(state = initialState, action) {
  switch (action.type) {
    case GET_ORDERS:
      return {
        ...state,
        orders: [...action.payload],
      };
    case GET_ORDERS_CALCULATIONS:
      return {
        ...state,
        ...action.payload,
      };

    default:
      return state;
  }
}
